.cases {
  height: 50vh;
  // height: calc(var(--vh, 1vh) * 50 );
  .cases-navigation {
    position: absolute;
    bottom: 0px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 6;
    padding: 32px;
    box-sizing: border-box;
    @include media('<=tablet') {
      display: none;
    }
    .cases-arrow {
      background: rgba(0,0,0, .4);
      height: 72px;
      width: 72px;
      border-radius: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        color: $white;
        height: 28px;
        width: 28px;
      }
      &.disabled {
        opacity: 0.3;
      }
    }
  }

  .row {
    @include media ('<=tablet') {
      flex-direction: column
    }
    .case {
      position: relative;
      background: $black;
      cursor: pointer;
      &:hover {
        .case-image {
          opacity: .4;
        }
      }
      .case-details {
        width: 33.333vw;
        height: 50vh;
        height: calc(var(--vh, 1vh) * 50);
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 32px;
        box-sizing: border-box;
        z-index: 1;
        position: relative;
        @include media ('<=tablet') {
          width: 100vw;
        }
        @include media ('<=phone') {
          padding: 16px;
        }
        span {
          margin-top: 156px;
          font-size: 1.6rem;
          opacity: .8;
          color: $white;
          font-weight: 600;
          @include media ('<=desktop', '>tablet') {
            font-size: 1.4rem;
          }
          @include media ('<=phone') {
            font-size: 1.2rem;
            line-height: 2.2rem;
          }
        }
        h2 {
          font-size: 2.4rem;
          line-height: 3.4rem;
          width: 85%;
          margin-top: 16px;
          color: $white;
          @include media ('<=desktop', '>tablet') {
            font-size: 2rem;
            line-height: 2.4rem;
          }
          @include media ('<=phone') {
            font-size: 1.7rem;
            line-height: 2.2rem;
            margin-top: 8px;
          }
        }
      }
      .case-image {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: absolute;
        opacity: .65;
        transition: .4s cubic-bezier(.6, -.05, .1, .99);
        ing {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
}
